.MenuItem {
    margin: 0;
    display: block;
    height: 100%;
    width: auto;
    align-items: center;
}
.MenuItem a {
    text-decoration: none;
    color: #8f5c2c;
    height: 100%;
    padding: 16px 10px;
    display: block;
    
}
.MenuItem a:hover ,
.MenuItem a:active,
.MenuItem a.active {
    color: #40a4c8;
    
}


@media(min-width: 500px)
{
    .MenuItem {
        margin: 0;
        display: flex;
        height: 100%;
        width: auto;
        align-items: center;
    }
    .MenuItem a {
        text-decoration: none;
        color: white;
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
    }
    .MenuItem a:hover ,
    .MenuItem a:active,
    .MenuItem a.active {
        background: #8f5c2c;
        border-bottom: 4px solid #40a4c8;
    }
}
