.BuildControl {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 5px 0;
}
.BuildControl button {
    display: block;
    font: inherit;
    padding: 5px;
    margin: 0 5px;
    width: 80px;
    border: 1px solid #aa6817;
    cursor: pointer;
    outline: none ;
}
.BuildControl button:disabled {
    background: #ac9980;
    border: 1px solid #7e7365;
    color: #ccc;
    cursor: default;
}
.BuildControl button:hover:disabled {
    background: #ac9980;
    color: #ccc;
    cursor: not-allowed;
}
.Label {
    padding: 10px;
    font-weight: bold;
    width: 100px;
    text-align: right;
}
.BuildControl .Less {
    background: #d39952;
}
.BuildControl .More {
    background: #8f5e1e;
    color: white;
}